.baners {
	margin-top: 10px;
	grid-column: -2;
	grid-row: 1;
	//+r(991)
	//	grid-area: -2 / 1/ end / end
	@include r(767) {
		text-align: center;
		font-size: 0; } }
.baner {
	width: 183px;
	height: 210px;
	margin-bottom: 36px;
	display: block;
	&:last-of-type {
		margin-bottom: 0; }
	@include r(991) {
		width: 140px;
		height: 160px; }
	@include r(767) {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 0; }

	&--big {
		height: 240px;
		@include r(991) {
			height: 190px; }
		@include r(767) {
			margin-left: 20px; }
		@include r(399) {
			margin-left: 10px; } }
	img {
		width: 100%;
		height: 100%; } }
