.home-content {
	padding: 0 0px;
	font-size: 13px;
	color: $main-black;
	line-height: 1.24;
	grid-column: 2;
	grid-row: 1;
	@include clr;
	@include r(767) {
		margin-bottom: 20px; }
	//align-self: start
	//+r(991)
	//	grid-column: 2 / end
	h1 {
		margin-bottom: 19px; }
	img {
		width: 150px;
		height: 150px;
		&:nth-of-type(1) {
			float: right;
			margin-left: 15px; }
		&:nth-of-type(2) {
			float: left;
			margin-right: 10px; }
		@include r(550) {
			width: 120px;
			height: 120px; } }
	p {
		margin-bottom: 10px; } }
